@import url('https://fonts.googleapis.com/css?family=Unica+One&display=swap');
@import '/style/vars.scss';


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100vh;
    font-size: 62.5%;
    color: $color-black;
    background-color: $color-white;
    overflow: hidden;
}

.main {
    width: 50%;
    height: 50%;
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    margin: auto;
    border-collapse: collapse;
    tr:first-of-type .cell{
        border-top: none;
    }
    tr:last-of-type .cell{
        border-bottom: none;
    }
    tr .cell:first-of-type{
        border-left: none;
    }
    tr .cell:last-of-type{
        border-right: none;
    }
    tr:first-of-type .cell:first-of-type{
        border-radius: 1rem 0 0 0;
    }
    tr:first-of-type .cell:last-of-type{
        border-radius: 0 1rem 0 0;
    }
    tr:last-of-type .cell:first-of-type{
        border-radius: 0 0 0 1rem;
    }
    tr:last-of-type .cell:last-of-type{
        border-radius: 0 0 1rem 0;
    }
    .cell {
        border: 3px solid $color-white;
        width: 5rem; height: 5rem;
        cursor: pointer;
        text-align: center;
        vertical-align: middle;
        font-family: 'Unica One', cursive;
        font-size: 4rem;
        color: $color-white;
    }
}

.endgame{
    display: none;
    position: relative;
    // display: flex; // !!! gonna be displayed flex !!! 
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background-image: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);
    width:12rem; height: 12rem;
    top: 50%; left: 50%; transform: translate(-50%, -50%);
    border-radius: 5px;
    font-family: 'Unica One', cursive;
    text-align: center;
    text-transform: uppercase;
    .winner {
        color: $color-white;
        font-size: 2rem;
        letter-spacing: 1px;
    }
    button, button:hover {
        text-transform: uppercase;
        color: $color-white !important; 
        letter-spacing: 3px;
        background: none;
        color: inherit;
        border: none;
        border-radius: 5px;
        padding: 1.3rem 2.1rem;
        cursor: pointer;
        outline: none;
        background-image: linear-gradient(to bottom, rgb(176, 85, 221) 0%, rgb(121, 24, 121) 100%);
        &:hover, &:active {
            background-image: none;
            background-color: rgb(121, 24, 121);
        }
    }
}

@import '/style/welcome_screen.scss';