.choose {
    // display: none;
    margin: 0;
    position: relative;
    z-index: 1000; // highest value!!!
    width: 100%;
    height: 100vh;
    display: flex;
    & > * {
        width: 50%;
        height: 100vh;
        transition: all 0.2s;
        overflow: hidden;
    }
    .left {
        background-image: linear-gradient(120deg, #f093fb 0%, #f5576c 100%);
        text-align: center;
        img {
            width: 15rem;
            position: relative;
            @media only screen and (max-width: 768px) {
                width: 10rem;
            }
        }
        .onleft{
            top: 30%;
            float: left;
            transform: translateY(-52%);
            margin-left: -7.5rem;
            @media only screen and (max-width: 768px) {
                top: 10%;
                margin-left: -5rem;
            }
        }
        .onright{
            top: 65%;
            float: right;
            transform: translateY(-65%);
            margin-right: -7.5rem;
            @media only screen and (max-width: 768px) {
                top: 60%;
                margin-right: -5rem;
            }
        }
        .text{
            @media only screen and (max-width: 480px) {
                margin-left: -3rem;
            }
            @media only screen and (max-width: 300px) {
                margin-left: -5rem;
            }
        }
    }
    .right {
        background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
        img {
            width: 15rem;
            position: relative;
            top: 50%; left: 50%;
            transform: translateX(-50%) translateY(50%);
            &:last-of-type{
                visibility: hidden;
                margin-left: -15rem;
                filter: blur(3px);
                @media only screen and (max-width: 768px) {
                    transform: translateX(-50%) translateY(-110%);
                    margin-left: 0;
                }
            }
            @media only screen and (max-width: 768px) {
                transform: translateX(-50%) translateY(-10%);
            }
        }
    }
    .text {
        width: 100%;
        font-family: 'Unica One', cursive;
        font-size: 1.8rem;
        color: $color-white;
        text-align: center;
        position: relative;
        z-index: 50;
        top: 50%;
        transition: all 0.4s;
        text-shadow: 0px 0px 5px rgba(0,0,0,0.75);
        @media only screen and (max-width: 768px) {
            font-size: 1.2rem;
            top: 40%;
        }
        &:hover{
            text-shadow: 0px 0px 2px rgba(0,0,0,0.5);
        }
    }
    cursor: pointer;
}